import React, { Component } from 'react';
import { Dropdown, ButtonGroup } from 'react-bootstrap';
import { FacebookShareButton, TwitterShareButton, FacebookIcon, TwitterIcon } from "react-share";
import { Card, Form, Button, Col } from "react-bootstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave, faPlusSquare, faUndo } from "@fortawesome/free-solid-svg-icons"
import axios from "axios";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";


export class Dropdowns extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      posts: [],
      pageValue : 0,
      isLoading: true,
      imgUrl: "https://jovialconnection.com/img/autopost/",
    }
  }


  handleClick(value) {
        this.props.history.push({
      pathname: '/updatepost/', state : { "a":2 }
    })
  }




  componentDidMount() {
    const apiUrl = 'https://jovialconnection.com/autopostApi/postJson.php';
    fetch(apiUrl)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            posts: result,
            isLoading: false,
          });
        },
        (error) => {
          this.setState({ error });
        }
      )
  }

  

  render() {
    const { error, posts, imgUrl } = this.state;
    return (
      <div>
        <div className="page-header">
          <h3 className="page-title">
            Posts Listing Pages
          </h3>
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb">
              <li className="breadcrumb-item"><a href="!#" onClick={event => event.preventDefault()}>Add posts</a></li>
              <li className="breadcrumb-item active" aria-current="page">All Posts</li>
            </ol>
          </nav>
        </div>
        <div className="row ">
          <div className="col-12 grid-margin">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title">Order Status</h4>
                <div className="table-responsive">
                  <table className="table">
                    <thead>
                      <tr>
                        <th> No. </th>
                        <th> Image  </th>
                        <th> Title </th>
                        <th> Content </th>
                        <th> Description </th>
                        <th> Share on </th>
                        <th> Edit </th>
                      </tr>
                    </thead>
                    <tbody>
                      {!this.state.isLoading ? (
                        posts.map(post => (
                          <tr key={post.id} >
                            <td>({post.id}) </td>
                            <td>
                              <div className="d-flex">
                                <img src={post.meta_img ? imgUrl + post.meta_img : imgUrl + 'yes.jpg'} alt="face" />
                                <span className="pl-2"></span>
                              </div>
                            </td>
                            <td> {post.title.substring(1, 25) + '...'}  </td>
                            <td> {post.meta_content.substring(1, 25) + '....'} </td>
                            <td> {post.meta_name.substring(1, 30) + '....'} </td>
                            <td>
                              <div className="badge badge-outline-success">
                                <a  href={"/post/share/"+post.id}>Share</a>
                              </div>
                            </td>
                            <td>
                              <div className="badge badge-outline-warning">
                                <a href={"/updatepost/"+post.id}>Edit</a>
                              </div>
                            </td>
                          </tr>

                        ))) : (
                        <h3>Loading...</h3>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    )
  }
}

export default Dropdowns;
